import React, { useState } from 'react';
import {
  FormControl,
  FormControlLabel,
  Radio,
  RadioGroup,
  Typography,
  Button,
  Box,
  Paper,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions
} from '@mui/material';
import { Formik, Form } from 'formik';
import * as Yup from 'yup';

const validationSchema = Yup.object().shape({
  income: Yup.string().required('   '),
  severancePay: Yup.string().required('      ')
});

const IncomeStep = ({ onNext, data }) => {
  const [showDialog, setShowDialog] = useState(false);

  const initialValues = {
    income: data.income || '',
    severancePay: data.severancePay || ''
  };

  const handleSubmit = (values) => {
    if (values.income === 'below7000' && values.severancePay === 'no') {
      setShowDialog(true);
    } else {
      onNext(values);
    }
  };

  return (
    <Formik
      initialValues={initialValues}
      validationSchema={validationSchema}
      onSubmit={handleSubmit}
    >
      {({ values, handleChange, errors, touched }) => (
        <Form>
          <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
            <Typography variant="h5" gutterBottom align="center">
                  7,000  ?
            </Typography>

            <FormControl component="fieldset" fullWidth error={touched.income && Boolean(errors.income)}>
              <RadioGroup
                name="income"
                value={values.income}
                onChange={handleChange}
              >
                <FormControlLabel
                  value="above7000"
                  control={<Radio />}
                  label=""
                />
                <FormControlLabel
                  value="below7000"
                  control={<Radio />}
                  label=""
                />
              </RadioGroup>
              
              {touched.income && errors.income && (
                <Typography color="error" variant="caption">
                  {errors.income}
                </Typography>
              )}
            </FormControl>
          </Paper>

          <Paper elevation={3} sx={{ p: 3 }}>
            <Typography variant="h5" gutterBottom align="center">
                6      /   35% ?
            </Typography>

            <FormControl component="fieldset" fullWidth error={touched.severancePay && Boolean(errors.severancePay)}>
              <RadioGroup
                name="severancePay"
                value={values.severancePay}
                onChange={handleChange}
              >
                <FormControlLabel
                  value="yes"
                  control={<Radio />}
                  label=""
                />
                <FormControlLabel
                  value="no"
                  control={<Radio />}
                  label=""
                />
              </RadioGroup>
              
              {touched.severancePay && errors.severancePay && (
                <Typography color="error" variant="caption">
                  {errors.severancePay}
                </Typography>
              )}
            </FormControl>
          </Paper>

          <Box sx={{ mt: 4, display: 'flex', justifyContent: 'center' }}>
            <Button
              variant="contained"
              color="primary"
              type="submit"
              size="large"
            >
              
            </Button>
          </Box>

          <Dialog 
            open={showDialog} 
            onClose={() => setShowDialog(false)}
            maxWidth="sm"
            fullWidth
          >
            <DialogTitle align="center">
                
            </DialogTitle>
            <DialogContent>
              <Typography align="center">
                   -7,000 "    /, 
                      .
              </Typography>
            </DialogContent>
            <DialogActions sx={{ justifyContent: 'center', pb: 2 }}>
              <Button 
                onClick={() => setShowDialog(false)} 
                variant="contained" 
                color="primary"
              >
                
              </Button>
            </DialogActions>
          </Dialog>
        </Form>
      )}
    </Formik>
  );
};

export default IncomeStep;
