import React from 'react';
import {
  FormControl,
  FormControlLabel,
  Radio,
  RadioGroup,
  Typography,
  Button,
  Box,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions
} from '@mui/material';
import { Formik, Form } from 'formik';
import * as Yup from 'yup';

const validationSchema = Yup.object().shape({
  employmentStatus: Yup.string().required('   ')
});

const EmploymentStatusStep = ({ onNext, data }) => {
  const [showDialog, setShowDialog] = React.useState(false);
  const [dialogType, setDialogType] = React.useState('');

  const handleSubmit = (values) => {
    if (values.employmentStatus === 'notEmployed') {
      setDialogType('notEmployed');
      setShowDialog(true);
    } else if (values.employmentStatus === 'selfEmployed') {
      setDialogType('selfEmployed');
      setShowDialog(true);
    } else {
      onNext({ employmentStatus: values.employmentStatus });
    }
  };

  const initialValues = {
    employmentStatus: data.employmentStatus || ''
  };

  return (
    <Formik
      initialValues={initialValues}
      validationSchema={validationSchema}
      onSubmit={handleSubmit}
    >
      {({ values, handleChange, errors, touched }) => (
        <Form>
          <Typography variant="h5" gutterBottom align="center">
                 2019 - 2024?
          </Typography>
          <Typography variant="subtitle1" gutterBottom align="center" color="textSecondary">
            (     )
          </Typography>

          <FormControl component="fieldset" fullWidth error={touched.employmentStatus && Boolean(errors.employmentStatus)}>
            <RadioGroup
              name="employmentStatus"
              value={values.employmentStatus}
              onChange={handleChange}
            >
              <FormControlLabel
                value="employed"
                control={<Radio />}
                label=" "
              />
              <FormControlLabel
                value="selfEmployed"
                control={<Radio />}
                label=" "
              />
              <FormControlLabel
                value="notEmployed"
                control={<Radio />}
                label=" "
              />
              <FormControlLabel
                value="bothEmployedAndSelfEmployed"
                control={<Radio />}
                label="    "
              />
            </RadioGroup>
            
            {touched.employmentStatus && errors.employmentStatus && (
              <Typography color="error" variant="caption">
                {errors.employmentStatus}
              </Typography>
            )}
          </FormControl>

          <Box sx={{ mt: 4, display: 'flex', justifyContent: 'center' }}>
            <Button
              variant="contained"
              color="primary"
              type="submit"
              size="large"
            >
              
            </Button>
          </Box>

          <Dialog open={showDialog} onClose={() => setShowDialog(false)}>
            <DialogTitle>
              {dialogType === 'notEmployed' ? '  ' : ' '}
            </DialogTitle>
            <DialogContent>
              <Typography>
                {dialogType === 'notEmployed' 
                  ? '      2019 - 2024          '
                  : '      2019 - 2024      .             .            '}
              </Typography>
            </DialogContent>
            <DialogActions>
              <Button onClick={() => setShowDialog(false)} color="primary">
                
              </Button>
            </DialogActions>
          </Dialog>
        </Form>
      )}
    </Formik>
  );
};

export default EmploymentStatusStep;
