import React from 'react';
import {
  FormGroup,
  FormControlLabel,
  Checkbox,
  Typography,
  Button,
  Box,
  Paper
} from '@mui/material';
import { Formik, Form } from 'formik';
import * as Yup from 'yup';

const validationSchema = Yup.object().shape({
  additionalCriteria: Yup.array().of(Yup.string())
});

const criteriaOptions = [
  {
    value: 'unemployment',
    label: '  /    '
  },
  {
    value: 'propertyTax',
    label: '    '
  },
  {
    value: 'securities',
    label: '   '
  },
  {
    value: 'lifeInsurance',
    label: '     /  '
  },
  {
    value: 'pensionDeposit',
    label: '    '
  },
  {
    value: 'donations',
    label: '      190  '
  },
  {
    value: 'disability',
    label: '          90%'
  },
  {
    value: 'militaryService',
    label: '  /   -8  '
  },
  {
    value: 'education',
    label: '  /   -7  '
  },
  {
    value: 'rentalIncome',
    label: '    '
  },
  {
    value: 'newImmigrant',
    label: '  -10  '
  }
];

const AdditionalCriteriaStep = ({ onNext, data }) => {
  const initialValues = {
    additionalCriteria: data.additionalCriteria || []
  };

  return (
    <Formik
      initialValues={initialValues}
      validationSchema={validationSchema}
      onSubmit={(values) => {
        onNext({ additionalCriteria: values.additionalCriteria });
      }}
    >
      {({ values, setFieldValue }) => (
        <Form>
          <Typography variant="h5" gutterBottom align="center">
                    -6  
          </Typography>

          <Paper elevation={3} sx={{ p: 3, mt: 3 }}>
            <FormGroup>
              {criteriaOptions.map((option) => (
                <FormControlLabel
                  key={option.value}
                  control={
                    <Checkbox
                      checked={values.additionalCriteria.includes(option.value)}
                      onChange={(e) => {
                        const newCriteria = e.target.checked
                          ? [...values.additionalCriteria, option.value]
                          : values.additionalCriteria.filter(
                              (value) => value !== option.value
                            );
                        setFieldValue('additionalCriteria', newCriteria);
                      }}
                    />
                  }
                  label={option.label}
                />
              ))}
            </FormGroup>
          </Paper>

          <Box sx={{ mt: 4, display: 'flex', justifyContent: 'center' }}>
            <Button
              variant="contained"
              color="primary"
              type="submit"
              size="large"
            >
              
            </Button>
          </Box>
        </Form>
      )}
    </Formik>
  );
};

export default AdditionalCriteriaStep;
