import React, { useState, useEffect } from 'react';
import emailjs from '@emailjs/browser';
import { 
  Stepper, 
  Step, 
  StepLabel, 
  Button, 
  Container, 
  Typography,
  Box,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions
} from '@mui/material';
import MaritalStatusStep from './steps/MaritalStatusStep';
import EmploymentStatusStep from './steps/EmploymentStatusStep';
import IncomeStep from './steps/IncomeStep';
import JobHistoryStep from './steps/JobHistoryStep';
import AdditionalCriteriaStep from './steps/AdditionalCriteriaStep';
import PersonalDetailsStep from './steps/PersonalDetailsStep';

// Initialize EmailJS with your public key
emailjs.init("7ggM2WNflMbliCnaP"); // Replace with your public key from EmailJS

const TaxRefundForm = () => {
  const initialFormData = {
    maritalStatus: '',
    employmentStatus: '',
    income: '',
    severancePay: '',
    jobHistory: '',
    additionalCriteria: [],
    personalDetails: {}
  };

  const [activeStep, setActiveStep] = useState(0);
  const [formData, setFormData] = useState(initialFormData);
  const [score, setScore] = useState(0);
  const [leadQuality, setLeadQuality] = useState('');
  const [showSuccessDialog, setShowSuccessDialog] = useState(false);

  // Function to adjust iframe height
  useEffect(() => {
    const updateIframeHeight = () => {
      const height = document.documentElement.scrollHeight;
      window.parent.postMessage({ type: 'setHeight', height }, '*');
    };

    updateIframeHeight();
    window.addEventListener('resize', updateIframeHeight);
    return () => window.removeEventListener('resize', updateIframeHeight);
  }, [activeStep]);

  const calculateScore = (newData) => {
    let newScore = 0;
    let scoreDetails = [];
    
    //  
    if (newData.maritalStatus === 'married') {
      newScore += 3;
      scoreDetails.push('/: 3 ');
    } else if (newData.maritalStatus) {
      newScore += 2;
      scoreDetails.push('/ / / / /: 2 ');
    }
    
    //  
    if (newData.employmentStatus === 'employed') {
      newScore += 5;
      scoreDetails.push(': 5 ');
    } else if (newData.employmentStatus === 'bothEmployedAndSelfEmployed') {
      newScore += 3;
      scoreDetails.push(' + : 3 ');
    }
    
    //  
    if (newData.income === 'above7000') {
      newScore += 5;
      scoreDetails.push('  7,000 ": 5 ');
    }
    
    //  
    if (newData.jobHistory === 'changed') {
      newScore += 4;
      scoreDetails.push(' : 4 ');
    } else if (newData.jobHistory === 'same') {
      newScore += 2;
      scoreDetails.push('  : 2 ');
    }
    
    //  
    const additionalCriteriaPoints = {
      unemployment: ['  ', 3],
      propertyTax: [' ', 3],
      securities: ['  ', 3],
      lifeInsurance: [' ', 3],
      pensionDeposit: ['  ', 3],
      donations: ['', 3],
      disability: ['', 3],
      militaryService: [' "', 3],
      education: [' ', 3],
      rentalIncome: ['  ', 3],
      newImmigrant: [' ', 3]
    };
    
    newData.additionalCriteria?.forEach(criteria => {
      if (additionalCriteriaPoints[criteria]) {
        newScore += additionalCriteriaPoints[criteria][1];
        scoreDetails.push(`${additionalCriteriaPoints[criteria][0]}: ${additionalCriteriaPoints[criteria][1]} `);
      }
    });

    setScore(newScore);
    
    //   
    let quality = '';
    if (newScore >= 30) {
      quality = '  ';
    } else if (newScore >= 20) {
      quality = ' ';
    } else if (newScore >= 15) {
      quality = ' ';
    } else {
      quality = ' ';
    }
    
    setLeadQuality(quality);
    return { score: newScore, quality, details: scoreDetails };
  };

  const handleNext = (stepData) => {
    const newData = { ...formData, ...stepData };
    setFormData(newData);
    calculateScore(newData);
    
    if (activeStep === steps.length - 1) {
      handleSubmit(newData);
    } else {
      setActiveStep((prevStep) => prevStep + 1);
    }
  };

  const handleBack = () => {
    setActiveStep((prevStep) => prevStep - 1);
  };

  const formatDataForEmail = (data, scoreData) => {
    const criteriaLabels = {
      unemployment: '  ',
      propertyTax: ' ',
      securities: '  ',
      lifeInsurance: ' ',
      pensionDeposit: '  ',
      donations: '',
      disability: '',
      militaryService: ' "',
      education: ' ',
      rentalIncome: '  ',
      newImmigrant: ' '
    };

    const employmentStatusLabels = {
      employed: '',
      selfEmployed: '',
      bothEmployedAndSelfEmployed: ' + ',
      unemployed: ' '
    };

    return `
       :
      
       :
       : ${data.personalDetails.fullName || ' '}
      : ${data.personalDetails.phone || ' '}
      : ${data.personalDetails.email || ' '}
      
       :
       : ${data.maritalStatus === 'married' ? '/' : '/ / / / /'}
       : ${employmentStatusLabels[data.employmentStatus] || ' '}
       : ${data.income === 'above7000' ? ' 7,000 "' : ' -7,000 "'}
       : ${data.severancePay === 'yes' ? '' : ''}
        : ${data.jobHistory === 'changed' ? '' : ''}
      
       :
      ${data.additionalCriteria?.map(criteria => criteriaLabels[criteria]).join('\n')}
      
       :
      " : ${scoreData.score} 
       : ${scoreData.quality}
      
       :
      ${scoreData.details.join('\n')}
      
       : ${new Date().toLocaleString('he-IL', { timeZone: 'Asia/Jerusalem' })}
    `;
  };

  const handleSubmit = async (finalData) => {
    try {
      const scoreData = calculateScore(finalData);
      const emailContent = formatDataForEmail(finalData, scoreData);

      const templateParams = {
        to_email: 'omerh@yuvalim-ins.co.il',
        subject: `     - ${scoreData.quality}`,
        message: emailContent
      };

      await emailjs.send(
        'service_mg36429',
        'template_isk33ym',
        templateParams
      );

      setShowSuccessDialog(true);
      
    } catch (error) {
      console.error('Error submitting form:', error);
      // Add error handling logic here
    }
  };

  const resetForm = () => {
    setFormData(initialFormData);
    setActiveStep(0);
    setScore(0);
    setLeadQuality('');
    setShowSuccessDialog(false);
  };

  const steps = [' ', ' ', ' ', '  ', ' ', ' '];

  const getStepContent = (step) => {
    switch (step) {
      case 0:
        return <MaritalStatusStep onNext={handleNext} data={formData} />;
      case 1:
        return <EmploymentStatusStep onNext={handleNext} data={formData} />;
      case 2:
        return <IncomeStep onNext={handleNext} data={formData} />;
      case 3:
        return <JobHistoryStep onNext={handleNext} data={formData} />;
      case 4:
        return <AdditionalCriteriaStep onNext={handleNext} data={formData} />;
      case 5:
        return <PersonalDetailsStep onNext={handleNext} data={formData} />;
      default:
        return 'Unknown step';
    }
  };

  return (
    <Container maxWidth="md" sx={{ pb: 4 }}>
      <Box sx={{ width: '100%', mt: 4 }}>
        <Stepper activeStep={activeStep} alternativeLabel>
          {steps.map((label) => (
            <Step key={label}>
              <StepLabel>{label}</StepLabel>
            </Step>
          ))}
        </Stepper>
        
        <Box sx={{ mt: 4, mb: 2 }}>
          {activeStep > 0 && (
            <Button onClick={handleBack} sx={{ float: 'right' }}>
              
            </Button>
          )}
        </Box>

        <Typography variant="body2" align="center" sx={{ mb: 4 }}>
                 .
        </Typography>

        {getStepContent(activeStep)}

        <Dialog
          open={showSuccessDialog}
          onClose={resetForm}
        >
          <DialogTitle>  !</DialogTitle>
          <DialogContent>
            <DialogContentText>
                 .      .
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={resetForm}></Button>
          </DialogActions>
        </Dialog>
      </Box>
    </Container>
  );
};

export default TaxRefundForm;
